<template>
  <div id="background">
    <Header v-on:noteComeDown="noteComeDown" />
    <Note v-bind:checkedTitle="checkedTitle" v-bind:setChecked="setChecked" />
  </div>
</template>

<script>
import Header from "../partials/Header";
import Note from "../Note";

export default {
  name: "Home",
  components: { Header, Note },
  data() {
    return {
      checkedTitle: String,
      setChecked: false,
    };
  },
  methods: {
    noteComeDown(title, checked) {
      this.checkedTitle = title;
      this.setChecked = checked;
    },
  },
};
</script>

<style lang="scss">
#background {
  width: 100%;
  height: 100%;
  background: url("../../assets/images/forest-bg.jpg") no-repeat center;
  background-size: cover;
}
</style>
