<template>
  <div :class="setChecked ? 'note on' : 'note'">
    <ul>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
    </ul>
    <Readme
      v-bind:onOff="checkedTitle === 'readme' && setChecked ? onOff : !onOff"
    />
    <Portfolio
      v-bind:onOff="checkedTitle === 'portfolio' && setChecked ? onOff : !onOff"
    />
  </div>
</template>

<script>
import Readme from "./Readme";
import Portfolio from "./Portfolio";

export default {
  name: "Note",
  components: {
    Readme,
    Portfolio,
  },
  data() {
    return {
      onOff: true,
    };
  },
  props: { checkedTitle: String, setChecked: Boolean },
};
