<style lang="scss">
.note {
  position: relative;
  width: 545px;
  height: 0%;
  z-index: 1;
  margin-left: auto;
  right: 0;
  overflow: hidden;
  /* display:none; */
  transition: all 1s;
  ul {
    position: relative;
    width: 100%;
    height: 100%;
    background: rgba(245, 246, 250, 0.9);
    padding-top: 130px;
    box-sizing: border-box;
    border-top: 30px solid #130f40;
    overflow: hidden;
    li {
      width: 100%;
      padding-top: 40px;
      border-bottom: 1px dashed rgba(153, 153, 153, 0.25);
      position: relative;
    }
    li:first-child {
      border: none;
      padding-top: 0;
    }
  }
  ul::before {
    position: absolute;
    right: 0;
    top: 80px;
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    z-index: 3;
    background: #fab1a0;
  }
}
.note.on {
  height: 100%;
}
</style>
