<template>
  <header>
    <nav>
      <ul>
        <li
          v-for="item in items"
          v-bind:key="item.id"
          v-on:click="clicked(item.id)"
          :class="
            item.checked ? `menu__${item.title} clicked` : 'menu__item.title'
          "
        >
          <font-awesome-icon icon="check" class="check__icon" />{{ item.title }}
        </li>
      </ul>
    </nav>
  </header>
</template>

<script>
export default {
  name: "Header",
  components: {},
  data() {
    return {
      items: [
        { id: 0, title: "readme", checked: false },
        { id: 1, title: "portfolio", checked: false },
        { id: 2, title: "github", checked: false },
        { id: 3, title: "credits", checked: false },
      ],
    };
  },
  methods: {
    clicked(i) {
      let check = this.items[i].checked;
      this.items[i].checked = !check;
      const found = this.items.filter(
        (item) => item.title !== this.items[i].title
      );
      for (let a = 0; a < found.length; a++) {
        this.items.find(
          (item) => item.title === found[a].title
        ).checked = false;
      }
      this.$emit("noteComeDown", this.items[i].title, this.items[i].checked);
    },
  },
};
</script>

<style lang="scss">
header {
  position: fixed;
  top: 60px;
  right: 80px;
  z-index: 2;
  nav {
    ul {
      display: flex;
      li {
        position: relative;
        font-family: Gloria Hallelujah;
        font-weight: bold;
        font-size: 20px;
        color: #ffa801;
        letter-spacing: 1.5px;
        cursor: pointer;
        .check__icon {
          position: absolute;
          color: #ff6b6b;
          top: -20px;
          left: 50%;
          transform: scale(0.5) translateX(-50%);
          cursor: auto;
          transition: all 0.3s;
          opacity: 0;
        }
      }
      li:not(:last-child) {
        margin-right: 30px;
      }
      li:hover .check__icon {
        transform: scale(1) translateX(-50%);
        opacity: 1;
      }
      li.clicked {
        .check__icon {
          transform: scale(1) translateX(-50%);
          opacity: 1;
        }
      }
    }
  }
}
</style>
